.sc-resort__map__wrapper svg g.sold [id*=Unavailable],
.sc-resort__map__wrapper svg g.sold [id*=Available] {
  display: none;
}
.sc-resort__map__wrapper svg g.sold [id*=Sold] {
  display: block;
}
.sc-resort__map__wrapper svg.map-phase-all g.available {
  cursor: default;
}
.sc-resort__map__wrapper::before {
  @apply block pb-[75%] content-[""];
}
.sc-resort__map__house-tooltip {
  @apply absolute top-0 left-0 p-5 w-[188px] border border-white text-white font-bold text-center uppercase bg-[#1A6E8D] -translate-x-1/2 -translate-y-[105%];
